// Susy Setup
$small-range: 640px;
$medium-range: 1024px;
$large-range: 1440px;
$xlarge-range: 1920px;
$xxlarge-range:  1921px;

$susy: (
  columns: 12,
  math: fluid,
  output: fluid,
  gutter-position: inside,
);

@mixin screen-xlarge {
  @media only screen and (min-width: 1920) {
    @content;
  }
}

@mixin screen-large {
  @media only screen and (max-width: 1440px) {
    @content;
  }
}

@mixin screen-medium {
  @media only screen and (max-width: 1024px) {
    @content;
  }
}

@mixin screen-small {
  @media only screen and (max-width: 640px) {
    @content;
  }
}
